___CSS_LOADER_EXPORT___.push([module.id, `html,
body {
  border: 0;
  margin: 0;
  padding: 0;
  height: 100%;
  background-color: grey;
}

#content {
  margin: auto;
  height: 100%;
  max-width: 500px;
  display: grid;
  grid-template-rows: 100px 1fr 50px;
}

header {
  background-color: cadetblue;
  display: grid;
  justify-items: center;
  align-items: center;
  grid-template-rows: 60px 40px;
}

nav {
  background-color: azure;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  gap: 5px;
  overflow: auto;
}
nav > a {
  cursor: pointer;
  min-width: 80px;
  display: flex;
  justify-content: center;
  padding: 5px 0;
  border-radius: 5px;
  transition: 1s;
}

nav > a:hover {
  background-color: blue;
  color: white;
}
.selected {
  color: white;
  background-color: blue;
}
