::-webkit-scrollbar {
  height: 8px;
}
::-webkit-scrollbar-track {
  background: #f1f1f1;
}
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

main {
  background-color: white;
}

footer {
  background-color: cadetblue;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.github {
  height: 20px;
}
